import type { Observable } from '@sourcegraph/observable'
import { concatMap, promiseToObservable } from '@sourcegraph/observable'
import * as os from 'node:os'
import type { ResolvedConfiguration } from '../../../configuration/resolver'
import type { ToolRun } from '../../tool-service'
import { spawnAndObserveChildProcess } from '../node-helpers'
import type { BashToolDef } from './common'
import { loadProfileEnvironmentVariables } from './load-profile'
import { createXterm, getXtermContent } from './xterm'

export function runCommandWithNodeSpawn(
	cwd: string,
	config: ResolvedConfiguration,
	args: BashToolDef['args'],
): Observable<ToolRun<BashToolDef>> {
	const xterm = createXterm()
	return loadProfileEnvironmentVariables(
		cwd,
		config.settings['terminal.commands.node-spawn.load-profile'],
	).pipe(
		concatMap((env) =>
			spawnAndObserveChildProcess(args.cmd, [], {
				shell: os.userInfo().shell || true,
				cwd,
				stdio: ['ignore', null, null],
				env,
			}),
		),
		concatMap(({ lastData, exitCode, exited }): Observable<ToolRun<BashToolDef>> => {
			return promiseToObservable(
				new Promise((resolve) => {
					const buffer = lastData?.subarray() ?? new Uint8Array()
					xterm.write(buffer, () => {
						const { output } = getXtermContent(xterm)
						if (!exited) {
							resolve({
								status: 'in-progress',
								progress: { output },
							})
						}
						resolve({
							status: 'done',
							result: { output, exitCode: exitCode ?? -1 },
						})
					})
				}),
			)
		}),
	)
}
