import { concatMap, Observable, promiseToObservable } from '@sourcegraph/observable'
import * as os from 'node:os'
import { resolve } from 'path'
import { checkDirURIIsFile } from '../node-helpers'
import type { bashToolReg } from './common'
import { commandRequiresExplicitUserAcceptance, isPathOutsideCwd } from './common'
import { runCommandWithNodeSpawn } from './node-spawn'
import { Dynamic } from './shell-command-parser'

export const nodeBashTool: NonNullable<(typeof bashToolReg)['fn']> = (
	{ args, userInput },
	{ dir, config, toolMessages },
) => {
	checkDirURIIsFile(dir)

	const cwd = resolve(dir.fsPath, args.cwd ? expandHomeDir(args.cwd) : '.')

	const validationError = (error: string) => ({
		status: 'done' as const,
		result: {
			output: `Error: ${error}`,
			exitCode: 1,
		},
	})

	const cmdError = validateCmd(args.cmd)
	if (cmdError) {
		return Observable.of(validationError(cmdError))
	}

	const cwdError = validateCwd(cwd)
	if (cwdError) {
		return Observable.of(validationError(cwdError))
	}
	const validation = commandRequiresExplicitUserAcceptance(args.cmd, cwd, config.settings)
	const toAllow = validation.parts.flatMap((part) =>
		// Only offer adding to allowlist when parsable, non-dynamic, and within cwd.
		part.requiresConfirmation
			? typeof part.command === 'string' ||
				part.command.program === Dynamic ||
				part.command.arguments.some((arg) => arg === Dynamic || isPathOutsideCwd(arg, cwd))
				? []
				: [part.command.program]
			: [],
	)
	if (validation.requiresConfirmation) {
		if (userInput && !userInput.accepted) {
			return Observable.of({ status: 'rejected-by-user', toAllow })
		} else if (!userInput?.accepted) {
			return Observable.of({ status: 'blocked-on-user', toAllow })
		}
	}

	const runWith = config.settings['terminal.commands.environment'] ?? 'vscode-terminal'
	if (import.meta.env.VITE_PLATFORM === 'vscode' && runWith === 'vscode-terminal') {
		// This dynamic import shouldn't be required because `runCommandInVSCodeTerminal`
		// doesn't import `vscode`, it only imports it as a *type*. However, Vitest
		// doesn't seem to correctly handle type imports?
		return promiseToObservable(import('./vscode-terminal')).pipe(
			concatMap((terminal) =>
				terminal.runCommandInVSCodeTerminal(cwd, config, args, toolMessages),
			),
		)
	}
	return runCommandWithNodeSpawn(cwd, config, args)
}

const homeDirectory = os.homedir()

function expandHomeDir(path: string) {
	return homeDirectory ? path.replace(/^~(?=$|\/|\\)/, homeDirectory) : path
}

function validateCmd(cmd: unknown): string | undefined {
	if (typeof cmd !== 'string') {
		return '`cmd` must be a string representing a command.'
	}

	return undefined
}

function validateCwd(cwd: unknown): string | undefined {
	if (cwd === undefined) {
		return undefined
	}

	if (typeof cwd !== 'string') {
		return '`cwd` must be a string representing a relative path within the workspace.'
	}

	return undefined
}
