import { Terminal } from '@xterm/headless'
import { pluralize } from '../../../common/pluralize'
import { BASH_TOOL_MAX_CHARACTER_OUTPUT, type BashToolDefOutput } from './common'

export function createXterm(): Terminal {
	return new Terminal({
		cols: 1_000,
		rows: 500,
		allowProposedApi: true,
		convertEol: true,
		scrollback: 10_000,
		cursorBlink: false,
	})
}

export interface XtermContentParams {
	maxCharacterLength: number
}
const defaultContentParams: XtermContentParams = {
	maxCharacterLength: BASH_TOOL_MAX_CHARACTER_OUTPUT,
}

/**
 * Extracts formatted content from an XTerm terminal instance
 * @param terminal The XTerm terminal instance
 * @returns Clean, formatted terminal content
 */
export function getXtermContent(
	terminal: Terminal,
	params: XtermContentParams = defaultContentParams,
): BashToolDefOutput {
	const buffer = terminal.buffer.active
	const lines: string[] = []
	const maxCharacterLength = Math.max(0, params.maxCharacterLength)

	// Collect all lines from the buffer ignoring truncation settings.
	for (let y = 0; y < buffer.length; y++) {
		const line = buffer.getLine(y)
		if (!line) {
			continue
		}

		const lineText = line.translateToString(false).trimEnd()

		if (lineText.trim().length === 0 && lines.at(-1)?.trim().length === 0) {
			// Skip this line because it is empty and the previous line was also empty
			continue
		}

		lines.push(lineText)
	}

	// If necessary, return truncated output
	let characterCount = 0
	for (let i = lines.length - 1; i > 0; i--) {
		const line = lines[i]!
		characterCount += line.length
		const needsTruncation = characterCount > maxCharacterLength
		if (needsTruncation) {
			const truncatedLinesCount = i
			const truncatedLinesExplanation = `Truncated ${truncatedLinesCount} ${pluralize(truncatedLinesCount, 'line')} above this point`
			const visibleLinesCount = lines.length - i - 1
			const visibleLinesExplanation =
				visibleLinesCount === 1
					? `Below is the last line of the output`
					: `Below are the last ${visibleLinesCount} lines of the output`

			return {
				output: [
					`--- ${truncatedLinesExplanation}. ${visibleLinesExplanation}. ---`,
					...lines.slice(i),
				].join('\n'),
				hidden_truncatedOutput: lines.slice(0, i).join('\n'),
			}
		}
	}

	// No truncation needed, return the full output
	return {
		output: lines.join('\n'),
	}
}
